.navbar {
    width: 100%;
    background-color: #171717;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1.5rem 1rem;
    z-index: 50;
    height: 150px;
}
.image {
    height: 120px;
    width: auto;
}

.image-hidden {
    display: none;
}
.navbar > h1 {
    color: #f4f4f4;
    font-size: 3rem;
    font-family: Raleway;

    padding: 50px;
}
.hamburger__menu {
    width: 30px;
    height: 30px;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    
}
.hamburger__menu > div {
    width: 100%;
    height: 3px;
    background: #f4f4f4;
}

@media (max-width:960px) {
    .navbar {
        height: 110px;
    }
    .image {
        height: 90px;
        width: auto;
    }
    .navbar > h1 {
        font-size: 2rem;
        padding: 100px;
    }
    .hamburger__menu {
        display: flex;
    }
}
@media (max-width: 585px) {
    .navbar {
        height: 110px;
    }
    .image {
        height: 80px;
        width: auto;
    }
    .navbar > h1 {
        font-size: 1.3rem;
        padding: 50px;
    }
}

.footer {
    margin-top: 40px;
    width: 100%;
    background-color: #171717;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 1.5rem 1rem;
    z-index: 50;
    height: 130px;
    /* position: fixed; */
    left: 0;
    bottom: 0;
}
.footerItem p {
    color: #f4f4f4;
    font-size: 1.5rem;
    font-family: Raleway;
    font-weight: 700;
    cursor: pointer;
}
.footerItem:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
.footerItem:active {
    color: gray;
}
a {
    text-decoration: none;
}


@media (max-width:960px) {
    .footer {
        height: 110px;
        margin-top: 20px;
        
    }
    .footerItem p {
        font-size: 1.3rem;
    }
}
@media (max-width: 500px){
    .footerItem p {
        font-size: 1rem;
    }
}
.backdrop {
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 100;
    position: fixed;
    top: 0;
    left: 0;
}
.sidedrawer {
    width: 70%;
    height: 100vh;
    background: #fff;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 200;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: all 0.3s ease-out;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.sidedrawer.show {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.sidedrawer__links {
    display: flex;
    flex-direction: column;
    list-style: none;

}
.sidedrawer__links > li {
    display: flex;
    align-items: center;
}
.sidedrawer__links > li > a {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex: 1 1;
    padding: 1rem;
    text-decoration: none;
    color: #171717;
    font-size: 1.6rem;
}

.sidedrawer__links > li > a:hover {
    background: #171717;
    color: #f4f4f4;

}

.sidedrawer__links > li > a > span {
    display: flex;
    align-items: center;
    margin-left: 8px;
}

.sidedrawer__cartbadge {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: #171717;
    color: #f4f4f4;
    font-size: 1rem;
    margin-left: 8px;
}

.sidedrawer__links > li > a:hover .sidedrawer__cartbadge {
    background: #f4f4f4;
    color: #171717;
}
@media (min-width: 960px) {
    .sidedrawer {
        display: none;
    }
}
